<template>
	<view>
		<view class="address-container" @click="selAddress">
			<view v-if="address.id" class="address-wrap">
				<view class="address-item">
					<view class="address-info">
						<view class="address-info-top">
							<view class="address-info-name">收货人：{{address.name}}</view>
							<view class="address-info-tel">{{address.tel}}</view>
						</view>
						<view class="address-info-addr">
							收货地址：{{address.province}}{{address.city}}{{address.district}}{{address.address}}</view>
					</view>
					<view class="address-icon">
						<uni-icons type="forward"></uni-icons>
					</view>
				</view>
			</view>
			<view v-else class="address-add">添加地址</view>
		</view>

		<view class="order-container">
			<view class="order-wrap">
				<view class="order-product">
					<view class="order-product-item" v-for="(item,k) in orderProduct">
						<view class="order-product-pic">
							<image :src="item.pic"></image>
						</view>
						<view class="order-product-info">
							<view class="order-product-name">{{item.name}}</view>
							<view class="order-product-price">{{item.price}}</view>
						</view>
						<view class="order-product-num">
							x1
						</view>
					</view>
				</view>
				<view class="order-param mix-top">
					<view class="order-param-label">服务技师</view>
					<view class="order-param-value master-name">{{master.name}}</view>
				</view>
				<picker :value="timeIndex" :range="timeArray" @change="bindTimeChange">
					<view class="order-param">
						<view class="order-param-label">服务时间</view>
						<view class="order-param-value service-time">
							<text v-if="timeIndex==-1">请选择服务时间</text>
							<text v-else>{{timeArray[timeIndex]}}</text>
						</view>
						<view class="order-param-icon">
							<uni-icons type="forward" size="15" color="#24C373"></uni-icons>
						</view>
					</view>
				</picker>
				<block v-if="master.travel_expense==1">
					<view class="order-param mix-top">
						<view class="order-param-label">出行方式</view>
						<view class="order-param-value" style="padding-top: 15px;">
							<block v-if="travel_type==1">
								<view class="travel-item travel-item-selected">滴滴/出租</view>
								<view @click="changeTravelType(2)" class="travel-item">公交/地铁</view>
							</block>
							<block v-if="travel_type==2">
								<view @click="changeTravelType(1)" class="travel-item ">滴滴/出租</view>
								<view class="travel-item travel-item-selected">公交/地铁</view>
							</block>
						</view>
					</view>
					<view class="order-param" style="height: 50px;">
						<view class="order-param-label" style="flex: 1;color: #999;">
							全程共{{distance_msg}}；滴滴/出租起步价{{master.qibujia}}元；每公里{{master.taxi_fee}}元；公交/地铁每公里{{master.bus_fee}}元
						</view>
					</view>
					<view class="order-param">
						<view class="order-param-label">往返车费</view>
						<view class="order-param-value" style="color: rgb(255, 96, 81);">￥{{payInfo.travel_expense}}
						</view>
					</view>
				</block>
				<view class="order-param mix-top">
					<view class="order-param-label">备注</view>
					<view class="order-param-value">
						<input type="text" placeholder="在此输入备注" />
					</view>
					<view class="order-param-icon">
						<uni-icons type="forward" size="15"></uni-icons>
					</view>
				</view>
				<view class="order-param mix-top">
					<view class="order-param-pic">
						<image src="../../static/images/coupon.png"></image>
					</view>
					<view class="order-param-label">优惠券</view>
					<view class="order-param-value" @click="openCouponToast">
						<view class="order-param-coupon" v-if="!coupon_list[0]">
							无可用优惠券
							<uni-icons type="forward" size="12" color="#999"></uni-icons>
						</view>
						<view v-if="coupon_list[0]&&!coupon.id" class="order-param-coupon order-param-coupon-active">
							{{coupon_list.length}}张可用
							<uni-icons type="forward" size="12" color="#fff"></uni-icons>
						</view>
						<view v-if="coupon_list[0]&&coupon.id" class="order-param-coupon order-param-coupon-sel">
							{{coupon.name}}
							<uni-icons type="forward" size="12" color="#FD4950"></uni-icons>
						</view>
					</view>
				</view>
			</view>
		</view>

		<view class="pay-type-container">
			<view class="pay-type-header">支付方式</view>
			<view class="pay-type-wrap">
				<radio-group @change="payTypeChange">
					<label>
						<view class="pay-type-item">
							<view class="pay-type-icon">
								<image src="../../static/images/wechat-pay.png"></image>
							</view>
							<view class="pay-type-name">微信支付</view>
							<view class="pay-type-radio">
								<radio value="1" checked />
							</view>
						</view>
					</label>
					<label>
						<view class="pay-type-item">
							<view class="pay-type-icon">
								<image src="../../static/images/balance-pay.png"></image>
							</view>
							<view class="pay-type-name">余额支付<text
									style="color: #555; font-size: 12px;">（当前账户余额：{{user.balance}}元）</text></view>
							<view class="pay-type-radio">
								<radio v-if="user.balance>=payInfo.pay_fee" value="2" />
								<radio v-else disabled value="2" />
							</view>
						</view>
					</label>
				</radio-group>

			</view>
		</view>

		<view style="width: 100%;height: 60px;"></view>
		<view class="pay-wrap">
			<view class="pay-info">
				<text>合计：</text> ￥{{payInfo.pay_fee}}
			</view>
			<view @click="pay" class="pay-btn">立即支付</view>
		</view>

		<uni-popup ref="popup" background-color="#fff">
			<view>
				<view style="display: flex;">
					<view style="display: flex;width: 50px;"></view>
					<view style="display: flex;flex: 1;justify-content: center;align-items: center;height: 40px;">选择优惠券
					</view>
					<view @click="closeCouponToast"
						style="display: flex;width: 40px;justify-content: center;align-items: center;height: 40px;">
						<uni-icons type="close" size="20"></uni-icons>
					</view>
				</view>
				<view>
					<yj-coupon :list="coupon_list" :btnShow="true" @selCoupon="selCoupon"></yj-coupon>
				</view>
			</view>

		</uni-popup>

	</view>
</template>

<script>
	// var jWeixin = require('jweixin-module');
	let pay = require('../../utils/pay.js')
	import {
		createOrder,
		updateOrder,
		getUserInfo,
		couponRecord
	} from '@/utils/api.js'
	export default {
		data() {
			return {
				address: {},
				orderProduct: [],
				orderProductIds: [],
				master: {},
				timeArray: ['0-1点', '1-2点', '2-3点', '3-4点', '4-5点', '5-6点', '6-7点', '7-8点', '8-9点', '9-10点', '10-11点',
					'11-12点', '12-13点', '13-14点', '14-15点',
					'15-16点', '16-17点', '17-18点', '18-19点', '19-20点', '20-21点', '21-22点', '22-23点', '23-24点'
				],
				timeIndex: -1,
				btnStatus: true,
				payType: 1,
				user: {
					balance: ''
				},
				coupon_list: [],
				coupon: {
					id: 0,
					coupon_record_id: 0
				},
				payInfo: {
					pay_fee: 0,
					travel_expense: 0,
					bus_fee: 0,
					taxi_fee: 0,
					qibujia: 0, //起步价
				},
				travel_type: 1,
				distance_msg: '',
				distance: 0,
				rtData: {},
			}
		},
		onLoad() {
			this.master = uni.getStorageSync('selMaster')
			console.log('获取了缓存中技师的信息', this.master)
			this.orderProduct = uni.getStorageSync('selItem')
			this.orderProduct.forEach((item) => {
				this.orderProductIds.push(item.id)
			})
			uni.removeStorageSync('selMaster')
			uni.removeStorageSync('selItem')
			this.userInfo()
		},
		onShow() {
			if (uni.getStorageSync('selAddress')) {
				this.address = uni.getStorageSync('selAddress')
				// uni.removeStorageSync('selAddress')
				this.calcDistanceAndFee()
			}
			this.couponRecord()
		},
		onReady() {
			this.calcPayInfo()
		},
		methods: {
			calcDistanceAndFee() {
				let distance = this.distanceOf({
					latitude: this.master.latitude,
					longitude: this.master.longitude
				}, {
					latitude: this.address.latitude,
					longitude: this.address.longitude
				})
				console.log(distance)
				this.distance = (distance / 1000).toFixed(2)
				if (distance < 1000) {
					this.distance_msg = distance + '米'
				} else {
					this.distance_msg = (distance / 1000).toFixed(2) + '公里'
				}
				this.calcPayInfo()
			},
			changeTravelType(e) {
				this.travel_type = e
				this.calcPayInfo()
			},
			calcPayInfo() {
				this.payInfo.pay_fee = 0
				this.orderProduct.forEach((item) => {
					console.log(item)
					this.payInfo.pay_fee += parseFloat(item.price)
				})
				if (this.coupon.amount) {
					this.payInfo.pay_fee -= parseFloat(this.coupon.amount)
				}
				if (this.master.travel_expense == 1 && this.distance >= 1) {
					if (this.travel_type == 1) {

						console.log(this.master.qibujia)
						//this.payInfo.travel_expense = this.payInfo.taxi_fee = ((this.master.taxi_fee) * this.distance).toFixed(2)
						this.payInfo.travel_expense = this.payInfo.taxi_fee = ((this.master.taxi_fee) * this.distance +
							this.master.qibujia).toFixed(2)
						this.payInfo.pay_fee += parseFloat(this.payInfo.travel_expense)
					}
					if (this.travel_type == 2) {
						this.payInfo.travel_expense = this.payInfo.bus_fee = ((this.master.bus_fee) * 1).toFixed(2)
						this.payInfo.pay_fee += parseFloat(this.payInfo.travel_expense)
					}
				}
			},
			selCoupon(e) {
				console.log(e)
				this.coupon = e
				this.$refs.popup.close()
				this.calcPayInfo()
			},
			closeCouponToast() {
				this.$refs.popup.close()
			},
			openCouponToast() {
				this.$refs.popup.open('bottom')
			},
			async couponRecord() {
				const res = await couponRecord({
					ids: this.orderProductIds.join('&'),
					use_range: 'submit_order'
				})
				this.coupon_list = res.data.data
			},
			async userInfo() {
				const res = await getUserInfo()
				this.user = res.data.data
			},
			async pay() {

				if (this.timeIndex == -1) {
					uni.showToast({
						title: '请选择6服务时间',
						icon: 'none'
					});
					return;
				}
				let data = {}
				data.address_id = this.address.id
				data.master_id = this.master.id
				data.service_time = this.timeArray[this.timeIndex]
				this.btnStatus = false
				data.ids = this.orderProduct[0].id + '=1'
				data.coupon_id = this.coupon.coupon_record_id
				data.pay_type = this.payType
				data.travel_type = this.travel_type
				data.travel_expense = this.payInfo.travel_expense
				uni.showLoading()
				const res = await createOrder(data)
				uni.hideLoading()
				if (this.payType == 2) uni.switchTab({
					url: '/pages/order/list'
				})
				if (this.payType == 1) {
					pay.toPay(res.data.data, {
						cate: 'order'
					})
					return;
					if (uni.getStorageSync('agent_cate') == 'gzh') {
						this.rtData = res.data.data
						this.ghzPay()
					} else {
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: res.data.data.timeStamp,
							nonceStr: res.data.data.nonceStr,
							package: res.data.data.package,
							signType: res.data.data.signType,
							paySign: res.data.data.paySign,
							success: (r) => {
								updateOrder({
									id: res.data.data.id,
									cate: 1
								})
								uni.showModal({
									title: '支付成功',
									confirmText: '确定',
									showCancel: false,
									success() {
										uni.switchTab({
											url: '/pages/order/list'
										})
									}
								})
							},
							fail: function(err) {
								uni.showModal({
									title: '支付失败',
									showCancel: false,
									success() {
										uni.switchTab({
											url: '/pages/order/list'
										})
									}
								})
							},
							complete: () => {
								this.btnStatus = true
							}
						})
					}

				}

			},
			ghzPay() {
				var self = this;
				jWeixin.config({
					debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
					appId: self.rtData.appId, // 必填，公众号的唯一标识
					timestamp: self.rtData.timeStamp, // 必填，生成签名的时间戳
					nonceStr: self.rtData.nonceStr, // 必填，生成签名的随机串
					signature: self.rtData.paySign, // 必填，签名，见附录1
					jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
				});
				jWeixin.ready(function() {
					jWeixin.checkJsApi({
						jsApiList: ['chooseWXPay'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
						success: function(res) {
							console.log('checkjsapi Success')
							console.log(res);
						},
						fail: function(res) {
							console.log('res')
							console.log(res);
						}
					});
					jWeixin.chooseWXPay({
						timestamp: self.rtData
							.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
						nonceStr: self.rtData.nonceStr, // 支付签名随机串，不长于 32 位
						package: self.rtData.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
						signType: self.rtData.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
						paySign: self.rtData.paySign, // 支付签名
						success: function(res) {
							// 支付成功后的回调函数							
							console.log('paysuccess')
							console.log(res)
							updateOrder({
								id: self.rtData.id,
								cate: 1
							})
							uni.showModal({
								title: '支付成功',
								confirmText: '确定',
								showCancel: false,
								success() {
									uni.switchTab({
										url: '/pages/order/list'
									})
								}
							})

						},
						cancel: function(r) {},
						fail: function(res) {
							console.log('payfail')
							console.log(res)
							uni.showModal({
								title: '支付失败',
								showCancel: false,
								success() {
									// uni.switchTab({url:'/pages/order/list'})
								}
							})

						}
					});
				});

				jWeixin.error(function(res) {
					console.log('error')
					console.log(res)
					uni.showToast({
						icon: 'none',
						title: '支付失败',
						duration: 4000
					});
				});
			},
			bindTimeChange(e) {
				this.timeIndex = e.detail.value
			},
			selAddress() {
				uni.setStorageSync('isSelAddress', 1)
				uni.navigateTo({
					url: '/pages/address/list'
				})
			},
			payTypeChange(e) {
				console.log(e)
				this.payType = e.detail.value
			},

			rad(d) {
				return d * Math.PI / 180.0;
			},
			distanceOf(p1, p2) {
				console.log(p1, p2)
				var radLng1 = this.rad(p1.latitude);
				var radLng2 = this.rad(p2.latitude);
				var mdifference = radLng1 - radLng2;
				var difference = this.rad(p1.longitude) - this.rad(p2.longitude);
				var distance = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(difference / 2), 2) +
					Math.cos(radLng1) * Math.cos(radLng2) * Math.pow(Math.sin(mdifference / 2), 2)));
				distance = distance * 6378.137;
				distance = Math.abs(Math.round(distance * 10000) / 10);
				return distance;
			}

		}
	}
</script>

<style>
	.address-container {
		padding: 10px;
		background-color: #fff;
	}

	.address-add {
		background-color: var(--mainColor);
		color: #fff;
		border-radius: 8px;
		height: 36px;
		line-height: 36px;
		text-align: center;
	}

	.address-wrap {
		display: flex;
		flex: 1;
	}

	.address-item {
		display: flex;
		flex: 1;
	}

	.address-info {
		display: flex;
		flex: 1;
		flex-direction: column;
		font-size: 14px;
	}

	.address-info-top {
		display: flex;

	}

	.address-info-name {
		display: flex;
		flex: 1;
	}

	.address-info-tel {
		display: flex;
		flex: 1;
		justify-content: flex-end;
	}

	.address-info-addr {
		display: flex;
		flex: 1;
	}

	.address-icon {
		display: flex;
		width: 40px;
		justify-content: flex-end;
		align-items: center;
	}



	.order-container {
		padding: 10px;
	}

	.order-wrap {
		background-color: #fff;
		padding: 6px;
	}

	.order-product {
		padding: 10px 0;
	}

	.order-product-item {
		display: flex;
	}

	.order-product-pic {
		display: flex;
		width: 90px;
	}

	.order-product-pic image {
		width: 90px;
		height: 70px;
	}

	.order-product-info {
		display: flex;
		flex-direction: column;
		flex: 1;
		justify-content: center;
		padding-left: 10px;
	}

	.order-product-name {
		font-size: 16px;
		color: rgb(51, 51, 51);
		font-weight: bold;
		margin-bottom: 8px;
	}

	.order-product-price {
		color: rgb(255, 96, 81);
		font-size: 16px;
		font-weight: bold;
		line-height: 20px;
	}

	.order-product-num {
		display: flex;
		align-items: center;
		justify-content: center;
		padding-right: 10px;
	}

	.order-param {
		display: flex;
		height: 40px;
		align-items: center;
		padding-left: 5px;
	}

	.order-param-pic {
		display: flex;
		width: 26px;
	}

	.order-param-pic image {
		width: 20px;
		height: 20px;
	}

	.order-param-label {
		display: flex;
		width: 80px;
		font-size: 13px;
		color: rgb(51, 51, 51);
	}

	.order-param-value {
		display: flex;
		flex: 1;
		justify-content: flex-end;
	}

	.order-param-value input {
		width: 100%;
		text-align: right;
	}

	.order-param-icon {
		display: flex;
		width: 20px;
		justify-content: flex-end;
	}

	.order-param-coupon {
		display: flex;
		justify-content: flex-end;
		color: #999;
	}

	.order-param-coupon-active {
		color: #fff;
		background-color: rgb(253, 73, 80);
		padding: 2px 3px 2px 10px;
		border-radius: 100px;
		font-size: 12px;
	}

	.order-param-coupon-sel {
		color: #FD4950;
	}

	.master-name {
		font-weight: 600;
		padding-right: 5px;
	}

	.service-time {
		color: var(--mainColor);
	}

	.travel-item {
		width: 80px;
		height: 28px;
		line-height: 28px;
		text-align: center;
		margin-left: 10px;
		border: 1px solid #333;
		border-radius: 3px;
	}

	.travel-item-selected {
		background-color: var(--mainColor);
		color: #fff;
		border: 1px solid var(--mainColor);
	}

	.pay-wrap {
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
		display: flex;
		align-items: center;
		padding: 10px;
	}

	.pay-info {
		display: flex;
		flex: 1;
		color: rgb(255, 96, 81);
		font-size: 19px;
		align-items: center;
	}

	.pay-info text {
		font-size: 13px;
	}

	.pay-btn {
		width: 100px;
		background-color: var(--mainColor);
